* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  color: #222;
  background: #beadad;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, sans-serif;
}

.app {
  padding-top: 110px;
}

.header {
  z-index: 1000;
  background: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 80px;
  padding: 0 60px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 1px 6px #0000000f;
}

.header-left {
  align-items: center;
  gap: 24px;
  display: flex;
}

.logo img {
  border-radius: 18px;
  width: auto;
  height: 60px;
}

.location {
  flex-direction: column;
  display: flex;
}

.location-main {
  align-items: baseline;
  gap: 6px;
  font-size: 14px;
  display: flex;
}

.location-name {
  border-bottom: 2px solid #111;
  padding-bottom: 1px;
  font-weight: 700;
}

.location-area {
  color: #686b78;
}

.location-arrow {
  color: #fc8019;
  font-size: 10px;
}

.location-underline {
  background: #111;
  width: 36px;
  height: 1px;
  margin-top: 3px;
}

.header-right {
  align-items: center;
  gap: 32px;
  display: flex;
}

.nav-item {
  color: #3d4152;
  cursor: pointer;
  align-items: center;
  gap: 8px;
  font-size: 15px;
  font-weight: 500;
  transition: color .2s;
  display: flex;
}

.nav-item:hover {
  color: #fc8019;
}

.nav-icon {
  font-size: 18px;
}

.nav-tag-new {
  color: #fc8019;
  margin-left: 4px;
  font-size: 10px;
  font-weight: 700;
}

.nav-cart {
  font-weight: 600;
}

.nav-cart-count {
  border: 2px solid #3d4152;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  min-width: 22px;
  height: 22px;
  font-size: 12px;
  display: inline-flex;
}

.body {
  padding: 20px 60px 40px;
}

.search {
  max-width: 400px;
}

.search input {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  width: 100%;
  padding: 10px 15px;
  font-size: 16px;
  transition: border-color .2s, box-shadow .2s;
}

.search input:focus {
  border-color: #fc8019;
  outline: none;
  box-shadow: 0 0 0 3px #fc801933;
}

.res-list {
  grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
  gap: 24px;
  margin-top: 30px;
  display: grid;
}

.res-card {
  cursor: pointer;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 14px;
  padding: 14px 14px 16px;
  transition: transform .2s, box-shadow .2s, border-color .2s;
  box-shadow: 0 4px 8px #0000000a;
}

.res-card:hover {
  border-color: #ffd8b2;
  transform: translateY(-4px);
  box-shadow: 0 10px 24px #0000001f;
}

.res-card img {
  object-fit: cover;
  border-radius: 12px;
  width: 100%;
  height: 150px;
  margin-bottom: 10px;
}

.res-card h3 {
  margin-bottom: 4px;
  font-size: 17px;
  font-weight: 600;
}

.res-area {
  color: #686b78;
  margin-bottom: 4px;
  font-size: 13px;
}

.res-cuisines {
  color: #93959f;
  margin-bottom: 8px;
  font-size: 13px;
}

.res-meta {
  color: #3d4152;
  font-size: 13px;
  font-weight: 500;
}

.res-rating {
  color: #fff;
  background: #48c479;
  border-radius: 4px;
  padding: 2px 6px;
}

@media (width <= 900px) {
  .header {
    padding: 0 20px;
  }

  .body {
    padding: 20px;
  }
}

@media (width <= 600px) {
  .header {
    height: 72px;
    padding: 0 12px;
  }

  .header-right {
    display: none;
  }

  .app {
    padding-top: 80px;
  }
}

.mobile-menu-btn {
  cursor: pointer;
  background: none;
  border: none;
  margin-left: auto;
  font-size: 24px;
  display: none;
}

@media (width <= 900px) {
  .header {
    padding: 0 20px;
  }

  .body {
    padding: 20px;
  }
}

@media (width <= 768px) {
  .header {
    height: 70px;
    padding: 0 16px;
  }

  .header-left {
    gap: 12px;
  }

  .location-main {
    flex-wrap: wrap;
    row-gap: 2px;
  }

  .mobile-menu-btn {
    display: block;
  }

  .header-right {
    background: #fff;
    border-bottom: 1px solid #eee;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    padding: 12px 16px 16px;
    display: none;
    position: absolute;
    top: 70px;
    left: 0;
    right: 0;
  }

  .header-right.mobile-open {
    display: flex;
  }

  .app {
    padding-top: 90px;
  }
}

.filter {
  justify-content: flex-start;
  margin-top: -20px;
  margin-bottom: 1px;
  display: flex;
}

.filter-btn {
  color: #3d4152;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdcdc;
  border-radius: 8px;
  padding: 10px 18px;
  font-size: 15px;
  font-weight: 600;
  transition: all .2s ease-in-out;
  box-shadow: 0 2px 4px #0000000d;
}

.filter-btn:hover {
  color: #fc8019;
  background: #fff5e9;
  border-color: #fc8019;
  transform: translateY(-2px);
}

.filter-btn:active {
  transform: scale(.96);
}

.filter-btn.active {
  color: #fff;
  background: #fc8019;
  border-color: #fc8019;
}
